const Buffer = require('buffer').Buffer
const path = require('path')

function depromise (p, cb) {
  if (cb) {
    p.then(resp => {
      cb(null, resp)
    }).catch(err => {
      cb(err)
    })
  } else return p
}

class ChromeStorageFSError extends Error {
  constructor (message, code) {
    super(message)
    this.name = 'ChromeStorageFSError'
    this.code = code || 'EIO'
  }
}

class FSStats {

  constructor (data) {
    this.data = data
    this.dev = 0
    this.ino = 0
    this.rdev = 0
    this.nlink = 1
    this.blksize = 4096
    this.uid = 0
    this.gid = 0
    this.birthtime = new Date(0)
    this.fileData = null
    this.atime = new Date(0)
    this.mtime = new Date(0)
    this.ctime = new Date(0)
    this.mode = 100644
    this.blocks = 1

  }
  isBlockDevice () {
    return false
  }
  isCharacterDevice () {
    return false
  }
  isDirectory () {
    return Array.isArray(this.data)
  }
  isFIFO () {
    return false
  }
  isFile () {
    return (!Array.isArray(this.data) && typeof this.data !== 'undefined')
  }
  isSocket () {
    return false
  }
  isSymbolicLink () {
    return false
  }
  
}

class ChromeStorageFS {
  constructor (stype) {
    this.stype = stype || 'local'
    this.store = chrome.storage[this.stype]
  }

  // helpers
  mkroot () {
    return new Promise((resolve, reject) => {
      this.store.set({['/']: []}, () => {
        if (chrome.runtime.lastError) {
          reject(chrome.runtime.lastError)
        } else {
          resolve()
        }
      })
    })
  }

  initialize () {
    return this.get('/').then(l => {
      if (!(l) || !(l.length)) {
        return this.mkroot()
      }
    }).catch(e => {
      return this.mkroot()
    })
  }

  set (pth, data, cb) {
    return depromise(new Promise((resolve, reject) => {
      this.parentExists(pth).then(exists => {
        if (!exists) reject(new ChromeStorageFSError(`${path.dirname(pth)} does not exist`, 'ENOENT'))
        else {
          if (!Array.isArray(data)) data = data.toString('base64')
          this.store.set({[pth]: data}, () => {
            if (chrome.runtime.lastError) {
              reject(chrome.runtime.lastError)
            } else {
              resolve()
            }
          })
        }
      })
    }), cb)
  }

  get (pth, encoding, cb) {
    if (pth.length > 1 && pth.endsWith('/')) pth = pth.slice(0, pth.length-1)
    return depromise(new Promise((resolve, reject) => {
      this.store.get(pth, data => {
        if (data && data[pth]) {
          if (!Array.isArray(data[pth]) && typeof encoding === 'undefined') {
            resolve(Buffer.from(data[pth], 'base64'))
          } else if (encoding) {
            resolve(data[pth].toString(encoding))
          } else {
            resolve(data[pth])
          }
        } else if (chrome.runtime.lastError) {
          reject(chrome.runtime.lastError)
        } else {
          reject(new ChromeStorageFSError(`${pth} is not found`, 'ENOENT'))
        }
      })
    }), cb)
  }

  rm (pth, cb) {
    if (pth.length > 1 && pth.endsWith('/')) pth = pth.slice(0, pth.length-1)
    return depromise(new Promise((resolve, reject) => {
      this.store.remove(pth, () => {
        if (chrome.runtime.lastError) {
          reject(chrome.runtime.lastError)
        } else {
          resolve()
        }
      })
    }), cb)
  }

  addToDir (pth) {
    var dirname = `${pth.slice(0, pth.lastIndexOf('/'))}/`
    var filename = pth.slice(pth.lastIndexOf('/') + 1)
    return this.get(dirname).then(l => {
      if (typeof l === 'undefined') throw new RangeError(`${dirname} is not a directory.`)
      if (filename && l.indexOf(filename) === -1) {
        l.push(filename)
        if (dirname.length > 1 && dirname.endsWith('/')) dirname = dirname.slice(0, dirname.length-1)
        return this.set(dirname, l)
      }
    }).catch(console.error)
  }

  parentExists (pth) {
    return this.stat(path.dirname(pth)).then(l => {
      return l.isDirectory()
    }).catch(e => {
      return false
    })
  }

  // not a standard fs method
  mkdirp (pth, mode, cb) {
    if (typeof cb === 'undefined') {
      cb = mode
      mode = undefined
    }
    var pths = pth.split('/').filter(p => {
      return p.length > 0
    })
    if (pths.length === 0) pths = [""]
    var ps = []
    for (var p = 1; p <= pths.length; p++) {
      ps.push(this._mkdir(`/${pths.slice(0, p).join('/')}`, mode, cb))
    }
    return depromise(Promise.all(ps), cb)
  }

  // File system implementation
  appendFile (pth, data, encoding, mode, flag, cb) {
    if (typeof cb === 'undefined') {
      if (typeof mode === 'undefined') {
        if (typeof flag === 'undefined') {
          if (typeof encoding !== 'undefined') {
            cb = encoding
          }
        } else cb = flag
      } else cb = mode
    }
    return depromise(this.get(pth, encoding).then((contents) => {
      return this.writeFile(pth, contents + data)
    }).catch(e => {
      return this.writeFile(pth, data)
    }), cb)
  }
  lstat (pth, cb) {
    return depromise(this.get(pth).then(contents => {
      return new FSStats(contents)
    }), cb)
  }
  mkdir (pth, mode, cb) {
    if (typeof cb === 'undefined') cb = mode
    return depromise(new Promise((resolve, reject) => {
      this.get(pth, undefined, (e, l) => {
        if (l || typeof e === 'undefined') {
          reject(new ChromeStorageFSError(`${pth} already exists`, 'EEXIST'))
        } else {
          this.set(pth, []).then(() => {
            this.addToDir(pth).then(resolve).catch(reject)
          }).catch(reject)
        }
      })
    }), cb)
  }
  readdir (pth, cb) {
    return depromise(this.get(pth), cb)
  }
  readFile (pth, encoding, flag, cb) {
    if (typeof encoding === 'object') {
      if (encoding.flag) flag = encoding.flag
      if (encoding.encoding) encoding = encoding.encoding
      else encoding = undefined
    }
    if (typeof cb === 'undefined') {
      if (typeof flag === 'undefined') {
        if (typeof encoding !== 'undefined') {
          cb = encoding
        }
      } else cb = flag
    }
    return depromise(this.get(pth, encoding), cb)
  }
  rename (oldpth, newpth, cb) {
    var self = this
    return depromise(this.lstat(oldpth).then(c => {
      return self.rm(oldpth).then(() => {
        return self.set(newpth, c.data).then(() => {
          if (c.isDirectory()) {
            return Promise.all(c.data.map(l => {
              return self.rename(`${oldpth}/${l}`, `${newpth}/${l}`)
            }))
          }
        })
      })
    }), cb)
  }
  rmdir (pth, cb) {
    return this.rm(pth, cb)
  }
  stat (pth, cb) {
    return this.lstat(pth, cb)
  }
  unlink (pth, cb) {
    return this.rm(pth, cb)
  }
  writeFile (pth, data, encoding, flag, mode, cb) {
    if (typeof cb === 'undefined') {
      if (typeof mode === 'undefined') {
        if (typeof flag === 'undefined') {
          if (typeof encoding !== 'undefined') {
            cb = encoding
          }
        } else cb = flag
      } else cb = mode
    }
    return depromise(this.set(pth, data).then(() => {
      return this.addToDir(pth)
    }), cb)
  }
}

module.exports = {
  ChromeStorageFS: ChromeStorageFS,
  ChromeStorageFSError: ChromeStorageFSError
}
